import Expenses from './components/expenses/Expenses'
import NewExpense from './components/newExpense/NewExpense'
import './App.css'
import { ToastContainer, toast } from 'react-toastify'
import { CircularProgress } from '@mui/material'
import 'react-toastify/dist/ReactToastify.css'
import { useState, useCallback, useEffect } from 'react'

const expenses = [
	{
		id: 'e1',
		title: 'Toilet Paper',
		amount: 94.12,
		date: new Date(2020, 7, 14),
	},
	{
		id: 'e2',
		title: 'New TV',
		amount: 799.49,
		date: new Date(2022, 2, 12),
	},
	{
		id: 'e3',
		title: 'Car Insurance',
		amount: 294.67,
		date: new Date(2021, 2, 28),
	},
	{
		id: 'e4',
		title: 'New Desk (Wooden)',
		amount: 450,
		date: new Date(2021, 5, 12),
	},
]

function App() {
	const [array, setArray] = useState(expenses)
	const [isLoading, setIsLoading] = useState(false)
	const [error, setError] = useState(null)

	const notify = () => toast('УСПЕШНО ОТПРАВЛЕНО')

	async function addExpensesHandler(objectWithId) {
		setIsLoading(true)
		const response = await fetch(
			'https://expenses-ed41b-default-rtdb.firebaseio.com/expensses.json',
			{
				method: 'POST',
				body: JSON.stringify(objectWithId),
				headers: {
					'Content-Type': 'application/json',
				},
			},
		)
		setIsLoading(false)
		notify()
		const data = await response.json()
		setArray((prevState) => [...prevState, objectWithId])
		console.log(data)
	}

	const fetchMovieHandler = useCallback(async () => {
		setIsLoading(true)

		try {
			setIsLoading(true)
			const response = await fetch(
				'https://expenses-ed41b-default-rtdb.firebaseio.com/expensses.json',
			)
			if (!response.ok) {
				throw new Error('Something went wrong')
			}
			setIsLoading(false)
			const data = await response.json()

			console.log(data)

			for (let key in data) {
				
				expenses.push({
					id: key,
					title: data[key].title,
					amount: data[key].amount,
					date: new Date(data[key].date),
				})
			}

			setArray(array)
			setIsLoading(false)
		} catch (error) {
			console.log(error)
			setError(error.message)
		}
		setIsLoading(false)
	}, [])

	let content = <p>Found no movies</p>
	if (array.length > 0) {
		content = <Expenses expenses={array} />
	}
	if (error) {
		content = <p>{error}</p>
	}
	if (isLoading) {
		content = <CircularProgress />
	}

	useEffect(() => {
		fetchMovieHandler()
	}, [])

	return (
		<div className='App'>
			<ToastContainer />
			<section>
				<NewExpense onAddDataToArray={addExpensesHandler} />
			</section>
			<section>{content}</section>
			<div>
				<button onClick={notify}>Notify!</button>
			</div>
		</div>
	)
}

export default App

